import React, { useState } from "react";
import "./quotation.css";
import { jsPDF } from "jspdf";
import "jspdf-autotable";
import html2canvas from 'html2canvas';
// import transporter from './nodemailerConfig';
import axios from 'axios';

const imgData = 'data:image/png;base64,'

const imgData2 = 'data:image/png;base64,'

const Quotation = () => {
    const [grassTypes, setGrassTypes] = useState({
        japanese: { name: 'Japanese Grass', price: 3.5 },
        philippine: { name: 'Philipine Grass', price: 2.5 },
        cow: { name: 'Cow Grass', price: 1.5 },
    });
    const [sqftValues, setSqftValues] = useState({
        japanese: 0,
        philippine: 0,
        cow: 0,
    });
    const [includeInstallation, setIncludeInstallation] = useState(false);
    const [custName, setName] = useState('');
    const [email, setEmail] = useState('');
    const [phone, setPhone] = useState('');
    const [totalCost, setTotalCost] = useState(0);

    const handleSqftChange = (e, grassType) => {
        const value = e.target.value;
        setSqftValues({ ...sqftValues, [grassType]: value });
    };

    const handleInstallationChange = (e) => {
        setIncludeInstallation(e.target.checked);
    };

    const handleCalculate = () => {
        let total = 0;
        Object.keys(sqftValues).forEach((grassType) => {
            total += sqftValues[grassType] * grassTypes[grassType].price;
        });

        if (includeInstallation) {
            total *= 2; // Double the total cost for installation
        }

        setTotalCost(total);
    };

    const generatePDF = () => {
        const doc = new jsPDF();
        const dateOptions = {day: 'numeric', month: 'long', year: 'numeric'};
        const date = new Date().toLocaleDateString(undefined, dateOptions);
        var xOffset = doc.internal.pageSize.width / 2;
        var img = new Image();

        // doc.setTextColor(0,0,0)
        doc.addImage(imgData, 'PNG', 30, 16, 20, 20);
        doc.setFontSize(18);
        doc.setFont(undefined, 'bold');
        doc.text("EL NURSERY & AGRICULTURE", xOffset, 22, {align: 'center'});
        doc.setFont(undefined, 'normal');
        doc.setFontSize(11);
        doc.text("633-A, Mukim-D, Kuala Jalan Baru, 11000 Balik Pulau, Penang.", xOffset, 27, {align: 'center'});
        doc.setFontSize(11);
        doc.text("Tel: 04-6421476 H/P: 016-6464678", xOffset, 32, {align: 'center'});
        doc.setFontSize(12);
        doc.text(`To: ${email}`, 14, 45);
        doc.text(`Date: ${date}`, 14, 53);
        doc.setFont(undefined, 'bold');
        doc.text(`Attn: ${custName} (${phone})`, 14, 61);
        doc.setFont(undefined, 'bold');
        doc.text("RE: Grass supply quotation", 14, 69);

        // Add table
        const tableColumn = ["Item No", "Grass Type", "Sqft", "Rate per Sqft (RM)", "Amount (RM)"];
        const tableRows = [];

        Object.keys(grassTypes).forEach((grassType, index) => {
            const grassTotal = (sqftValues[grassType] * grassTypes[grassType].price).toFixed(2);
            tableRows.push([
                index + 1,
                grassTypes[grassType].name,
                sqftValues[grassType],
                `RM${grassTypes[grassType].price}`,
                `RM${grassTotal}`,
            ]);

            if (includeInstallation) {
                const installationCost = grassTotal;
                tableRows.push([
                    // `${index + 1}-a`,
                    ' ',
                    `${grassTypes[grassType].name} Installation`,
                    // sqftValues[grassType],
                    ' ',
                    // `RM${grassTypes[grassType].price}`,
                    ' ',
                    `RM${installationCost}`,
                ]);
            }
        });

        doc.autoTable(tableColumn, tableRows, { startY: 77 , headStyles :{fillColor : [0, 0, 0]}});

        // Add total cost
        // tableRows.push(`Total Cost: $${totalCost.toFixed(2)}`, 14, doc.lastAutoTable.finalY + 10);
        doc.setFont(undefined, 'bold');
        doc.text(`Total Cost: RM${totalCost.toFixed(2)}`, 135, doc.lastAutoTable.finalY + 10);

        // Add signature
        doc.setFont(undefined, 'normal');
        doc.text("Thank you.", 14, doc.lastAutoTable.finalY + 26);
        doc.text("Yours faithfully,", 14, doc.lastAutoTable.finalY + 34);
        doc.setFont(undefined, 'bold');
        doc.text("EL NURSERY & AGRICULTURE ", 14, doc.lastAutoTable.finalY + 42);
        doc.addImage(imgData2, 'PNG', 14, doc.lastAutoTable.finalY + 42.3, 50, 20);
        doc.setFont(undefined, 'normal');
        doc.text("______________________________", 14, doc.lastAutoTable.finalY + 60);
        doc.text("Director: Eddy Loh", 14, doc.lastAutoTable.finalY + 68);

        // Save the PDF
        // doc.save("quotation.pdf");
        // const pdfData = doc.output('arraybuffer');
        // return pdfData;

        return doc.output('datauristring');
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        handleCalculate();
        // generatePDF();

        // const pdfData = generatePDF();

        // // Send email with PDF attachment
        // try {
        //     const response = axios.post('/send-quotation-email', {
        //         email: email,
        //         attachment: pdfData,
        //     });
        //     console.log('Email sent:', response.data);
        // } catch (error) {
        //     console.error('Error sending email:', error);
        // }

        const pdfDataUri = generatePDF();
        const emailData = {
            to_email: email,
            pdf: pdfDataUri,
        };

        axios.post('http://localhost:3002//send-email', emailData)
            .then(response => {
                console.log('Email sent successfully!', response.data);
            })
            .catch(error => {
                console.error('Error sending email:', error);
            });

        console.log('Form submitted!');
        // e.target.reset();
    };

    return (
        <section className="quotation section" id="quotation">
            <h2 className="section__title">Quotation</h2>
            <div className="quotation__container container grid">
                <form onSubmit={handleSubmit} className="quotation__form">
                    {Object.keys(grassTypes).map((grassType) => (
                        <div key={grassType} className="quotation__form-div">
                            <label className="quotation__form-label" htmlFor={grassType}>{grassTypes[grassType].name}</label>
                            <input className="quotation__form-input"
                                type="number"
                                id={grassType}
                                value={sqftValues[grassType]}
                                onChange={(e) => handleSqftChange(e, grassType)}
                            />
                            <label className="quotation__form-label2">sqft</label>
                        </div>
                    ))}
                    <div>
                        <label className="quotation__form-labelselect">
                            <input className="quotation__form-select"
                                type="checkbox"
                                checked={includeInstallation}
                                onChange={handleInstallationChange}
                            />
                            Include Installation Service
                        </label>
                    </div>
                    <div>
                        <button className="quotation__form-buttonCalculate" type="button" onClick={handleCalculate}>
                            Calculate
                        </button>
                        <input className="quotation__form-input" type="totalCost" value={totalCost} readOnly />
                    </div>
                    <div className="quotation__note">
                        <p className="quotation__note-title">Enter detail below and we will send you auto-generate quotation</p>
                    </div>
                    <div>
                        <label className="quotation__form-label" htmlFor="custName">Name:</label>
                        <input className="quotation__form-input" type="custName" id="custName" value={custName} onChange={(e) => setName(e.target.value)} required/>
                    </div>
                    <div>
                        <label className="quotation__form-label" htmlFor="email">Email:</label>
                        <input className="quotation__form-input" type="email" id="email" value={email} onChange={(e) => setEmail(e.target.value)} required/>
                    </div>
                    <div>
                        <label className="quotation__form-label" htmlFor="phone">Phone No:</label>
                        <input className="quotation__form-input"
                            type="tel"
                            id="phone"
                            value={phone}
                            onChange={(e) => setPhone(e.target.value)} required
                        />
                    </div>
                    <button className="quotation__form-button" type="submit">Submit</button>
                </form>
            </div>
        </section>
    )
};

export default Quotation;
